body {
  overflow-x: hidden;
}

#principal {
  min-height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  text-align: justify;
  background-image: url("/svgs/Rectangle-7.svg");
  background-position: center bottom;
  background-repeat: none;
  background-size: cover;
  z-index: -100;
  transition: 0.2s;
}

.textos {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  text-align: justify;
  padding-bottom: 50px;
  margin: 0 50px;
}

#blue {
  margin: 90px 0;
}

#title {
  font-family: "Josefin Sans";
  font-style: normal;
  font-size: 161px;
  line-height: 150px;
  margin-left: 15%;
  width: 550px;
}

#textoComum {
  width: 700px;
}

#textoComum p {
  line-height: 30px;
  font-size: 23px;
  margin-left: 10%;
}

#calculator,
#sack {
  font-size: 100px;
  padding-bottom: 13px;
  margin-left: -20px;
}

#green {
  color: #38ae59;
}

#blue {
  color: #5850fe;
}

#miniIcone {
  width: auto;
}

#textoComum {
  font-family: "Public Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 20px;
  text-align: justify;
  margin-left: 10%;
  margin-top: -100px;

  color: #898989;
}

.calcu {
  z-index: 2;
  position: absolute;
  right: 0;
  bottom: 0;
  padding-top: 10%;
  width: 100%;
  height: 100%;
}

.calcu img {
  width: 320px;
  margin-top: 5%;
  transform: skew(15deg);
  padding-top: 5%;
  position: absolute;
  bottom: 0;
  right: 300px;
}

.calcu-som {
  z-index: -1;
  position: absolute;
  right: 0;
  bottom: 0;
  padding-top: 10%;
  z-index: -4;
}

.calcu-som img {
  width: 320px;
  margin-top: 5%;
  transform: skew(15deg);
  padding-top: 5%;
  position: absolute;
  bottom: 170px;
  right: 380px;
  opacity: 0.05;
  filter: blur(3px);
}

#desenhosvg {
  position: absolute;
  right: 0;
  width: 100vw;
  height: 100%;
  z-index: -3;
}

