  .calculadora-toda {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    margin: 0 auto;
  }

  .calcu {
    z-index: 1;
    position: unset;
    padding-top: 0%;
