@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  overflow-x: hidden;
  scroll-behavior: smooth;
  scroll-padding-top: 6.5rem;
}

:root {
  --azul: #5850fe;
  --verde: #38ae59;
  --branco: #f1f7fc;
}

body {
  background-color: #f1f7fc;
}

*::-webkit-scrollbar {
  height: 0.5rem;
  width: 1rem;
}

*::-webkit-scrollbar-track {
  background-color: transparent;
}

*::-webkit-scrollbar-thumb {
  background-color: #5850fe;
  background-color: var(--azul);
}

body {
  overflow-x: hidden;
  overflow-y: hidden;
}

.titulo {
  text-align: center;
  padding: 30px 0;
}

.titulo h2 {
  font-size: 5rem;
  font-family: "Public Sans";
  color: #5850fe;
}

.titulo h2 span {
  color: #38ae59;
}

@media (max-width: 660px) {
  .titulo h2 {
    font-size: 4rem;
  }
}

@media (max-width: 425px) {
  .ferramentas .titulo h2 {
    font-size: 3rem;
  }
}

.contato .boxes {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 2% 0 5% 0;
  grid-gap: 10%;
  gap: 10%;
}

.contato .box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  width: 450px;
  height: 180px;
  outline: 3px solid #5950fe45;
  border-radius: 15px;
  position: relative;
}

.contato .box .icon {
  position: absolute;
  left: 7%;
  top: 7%;
  font-size: 40px;
  color: #38ae59;
}

.contato .box .texto1 {
  margin-top: -10px;
  font-family: "Public Sans";
  color: rgb(73, 73, 73);
}
.contato .box .texto1 h3 {
  font-size: 3rem;
}

.contato .box .texto2 h2 {
  font-size: 2.5rem;
}

.contato .box .texto2 {
  padding-top: 3rem;
  font-size: 20px;
  font-family: "Public Sans";
  color: rgb(73, 73, 73);
}

.contato .fale-conosco {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1% 0 5% 0;
}

.contato .fale-conosco button {
  font-size: 27px;
  font-family: "Public Sans";
  color: azure;
  background-color: #5850fe;
  box-shadow: 3px 3px 7px rgba(0, 0, 0, 0.356);
  padding: 17px 50px;
  border-radius: 15px;
  border: 2px solid#4842ca;
  cursor: pointer;
  transition: 0.2s;
}

.fale-conosco a {
  text-decoration: none;
  cursor: pointer;
}

.fale-conosco button:hover {
  background-color: #5850fe;
  box-shadow: 1px 1px 7px rgba(0, 0, 0, 0.295);
  -webkit-transform: scale(0.97);
          transform: scale(0.97);
}

.boxes a {
  text-decoration: none;
  color: rgb(73, 73, 73);
}

@media (max-width: 1000px) {
  .contato .box {
    margin-bottom: 90px;
    padding: 20px;
    width: 420px;
    height: 190px;
  }

  .contato .box .icon {
    font-size: 40px;
  }

  .contato .box .texto1 {
    font-size: 23px;
  }

  .contato .box .texto2 {
    font-size: 17px;
  }

  .contato .fale-conosco button {
    font-size: 24px;
  }

  .contato .box .texto1 {
    margin-top: -20px;
  }
}

@media (max-width: 460px) {
  .contato .box {
    margin: 0 5% 50px 5%;
    margin-bottom: 90px;
    padding: 20px;
    width: 300px;
    height: 130px;
  }

  .contato .boxes {
    padding: 2% 0 0% 0;
    grid-gap: 5%;
    grid-gap: 5%;
    gap: 5%;
  }

  .contato .box .icon {
    font-size: 20px;
  }

  .contato .box .texto1 {
    font-size: 15px;
    margin-top: -10px;
    justify-content: flex-start;
  }

  .contato .box .texto2 {
    font-size: 10px;
  }

  .contato .fale-conosco button {
    font-size: 13px;
  }

  .contato .box .texto1 {
    margin-top: -10px;
  }
  .contato .box .texto1 h3 {
    font-size: 2.2rem;
  }

  .contato .box .texto2 h2 {
    font-size: 2rem;
  }
}

body {
  overflow-x: hidden;
  overflow-y: hidden;
}

#green {
  color: #38ae59;
}

#blue {
  color: #5850fe;
}

.ferramentas {
  padding-bottom: 5%;
}

.ferramentas .container-box {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(36rem, 1fr));
  grid-gap: 3.5rem;
  gap: 3.5rem;
  padding: 50px;
  margin: 0px 80px 0 80px;
  outline: 3px solid #38ae59;
  padding: 5% 0 3% 0;
  margin: 0 10%;
  border-radius: 20px;
}

.ferramentas .container-box .box {
  background-image: linear-gradient(#5850fe, #38ae59);
  border-radius: 20px;
  width: 300px;
  height: 230px;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  box-shadow: -10px 10px 8px rgba(0, 0, 0, 0.137);
  transition: 0.2s;
}

.ferramentas .container-box .texto {
  font-family: "Public Sans";
  font-size: 2.8rem;
  width: 300px;
  text-align: center;
  padding: 20px 0;
  color: #5850fe;
}

.ferramentas .container-box .box .bg {
  background-color: #38ae59;
}

.ferramentas .container-box .boxEtexto {
  display: grid;
  justify-content: center;
}

.ferramentas .container-box a {
  text-decoration: none;
  transition: 0.2s;
}

.ferramentas .container-box a:hover .box {
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
  box-shadow: -1px 2px 8px rgba(0, 0, 0, 0.137);
}

@media (max-width: 660px) {
  .ferramentas .container-box .box {
    margin-left: 20%;
    justify-content: center;
    width: 180px;
    height: 170px;
    padding: 0px;
  }

  .ferramentas .container-box .boxEtexto {
    display: grid;
    justify-content: center;
    align-content: center;
  }

  .ferramentas .container-box .boxEtexto .texto {
    font-size: 22px;
  }

  .ferramentas .container-box .box .img img {
    width: 120px;
  }
}

@media (max-width: 425px) {
  .ferramentas .container-box .box {
    margin-left: 25%;
    justify-content: center;
    width: 150px;
    height: 120px;
    padding: 0px;
  }

  .ferramentas .container-box .boxEtexto {
    justify-content: center;
    align-content: center;
  }

  .ferramentas .container-box .boxEtexto .texto {
    font-size: 16px;
  }

  .ferramentas .container-box .box .img img {
    width: 90px;
  }

  .ferramentas .container-box {
    grid-template-columns: repeat(auto-fit, minmax(16rem, 1fr));
  }
}

