* {
  padding: 0;
  box-sizing: border-box;
}

.Navbar {
  font-size: 30px;
  font-weight: 700;
  line-height: 30px;
  font-style: normal;
  position: fixed;
  top: 0; /*  provavel erro */
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: #f1f7fc;
  box-shadow: 2px 2px 9px rgba(0, 0, 0, 0.12);
  font-family: "Josefin Sans";
  text-decoration: none;
  z-index: 10;
}

.Navbar .nav-logo {
  font-weight: 700;
  font-size: 35px;
  margin: 30px;
  color: #5850fe;
  text-decoration: none;
}

.Navbar .nav-logo .cash {
  font-weight: 700;
  font-size: 30px;
  color: #38ae59;
}

.Navbar .nav-items {
  margin-right: 30px;
}

.Navbar .nav-items a {
  color: #5850fe;
  font-size: 20px;
  text-decoration: none;
  margin: 30px;
  margin-right: 90px;
  position: relative;
  opacity: 0.9;
  transition: 0.3s;
}

.Navbar .nav-items a:hover {
  color: #38ae59;
  text-shadow: 2px 2px 3px rgba(0, 0, 0, 0.074);
}

.Navbar .nav-items a span:hover {
  color: #5850fe;
}

.Navbar .nav-items a span {
  color: #38ae59;
  transition: 0.3s;
}

.Navbar .nav-items a:hover {
  opacity: 1;
}

.Navbar .nav-items a::before {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 2px;
  background: #5850fe;
  transition: all 0.45s;
}

.Navbar .nav-items a:hover::before {
  width: 100%;
}

.Navbar .nav-toggle {
  display: none;
}

@media (max-width: 1550px) {
  .Navbar .nav-items a {
    font-size: 17px;
    margin-right: 20px;
  }

  .Navbar .nav-logo,
  .Navbar .nav-logo .cash {
    font-size: 27px;
  }
}

@media (max-width: 1000px) {
  .Navbar .nav-items a {
    font-size: 15px;
    margin-right: 10px;
  }

  .Navbar .nav-logo,
  .Navbar .nav-logo .cash {
    font-size: 25px;
  }
}

@media (max-width: 700px) {
  .Navbar .nav-logo {
    font-size: 25px;
    margin: 20px;
    margin-left: -50px;
    padding-top: 5px;
  }

  .Navbar .nav-items {
